import {request} from "@/network/request";

export function getDeviceBeatServeData () {
  return request({
    url:'/heartBeat/selectAll',
    method:'get',
    params:{
      currentPage:'1',
      pageSize:'10'
    }
  })
}

/**
 * 
 * @param pageNum 
 * @param pageSize 
 */
export function getCurrentDeviceBeatServeData (pageNum,pageSize) {
  return request({
    url:'/heartBeat/selectAll',
    method:'get',
    params:{
      currentPage:pageNum,
      pageSize:pageSize,
    }
  })
}

/**
 * 
 */
export function searchBeatServeData (obj) {
  return request({
    url:'/heartBeat/selectDevId',
    method:'get',
    params:obj
  })
}

/**
 * 
 */
export function askState () {
  return request({
    url:'/heartBeat/state',
    method:'post',
  })
}