import {request} from "@/network/request";

/**
 * 
 */

/**
 * 
 */
export function getRegionServeData () {
  return request({
    url:'/regional/selectAll',
    method:'get'
  })
}

/**
 * 
 * @param pageNum 
 * @param pageSize 
 */
export function getCurrentRegionServeData (pageNum,pageSize) {
  return request({
    url:'/regional/selectAll',
    method:'get',
    params:{
      currentPage:pageNum,
      pageSize:pageSize,
    }
  })
}

/**
 * 
 */
export function addRegionServeData (obj) {
  return request({
    url:'/regional/add',
    method:'post',
    data:obj,
    headers:{
      'Content-Type':'multipart/form-data'
    },
  })
}

/**
 * 
 */
export function deleteRegionServeData (id) {
  return request({
    url:`/regional/${id}`,
    method:'delete',
  })
}

/**
 * 
 */
export function updateRegionServeData (obj) {
  return request({
    url:'/regional/update',
    method:'put',
    data:obj,
    headers:{
      'Content-Type':'application/x-www-form-urlencoded'
    },
  })
}

/**
 * 
 */
export function getSiteName (id) {
  return request({
    url:'/regional/getSiteName',
    method:'get',
    params:{
      id:id
    }
  })
}
