<template>
    <div class="DevBody">
      <div class="dev-item">
        <div class="dev-item-line">
          <div class="dev-item-line-path">{{PageName}}</div>
          <div class="dev-item-line-button">
            <div class="dev-item-line-button-input">
              <el-input class="input-style" clearable v-model="searchForm.devNumber" size="medium" placeholder=""/>
              <el-input class="input-style" clearable v-model="searchForm.devName" size="medium" placeholder=""/>
              <el-select class="adjustment" clearable style="width: 150px" v-model="searchForm.siteName" placeholder="">
                <el-option v-for="(item) in this.store.siteAllData" :key="item.siteId" :label="item.siteName" :value="item.siteName"/>
              </el-select>
              <el-select class="adjustment" clearable style="width: 165px" v-model="searchForm.typeName" placeholder="">
                <el-option v-for="(item) in this.store.deviceTypeAllData" :key="item.id" :label="item.typeName" :value="item.typeName"/>
              </el-select>
              <el-button class="add-margin" type="primary" icon="el-icon-search" @click="getQuery"></el-button>
              <el-button style="width: 80px" type="info" @click="clearSearch"></el-button>
            </div>
            <el-button type="primary" @click="dialogFormVisible = true"></el-button>
          </div>
        </div>
        <div class="dev-item-table">
          <el-table
            :data="getOrSearch === 0? deviceData:deviceSearchData"
            style="width: 100%">
            <el-table-column align="center" fixed label="" min-width="100" prop="devNumber"/>
            <el-table-column align="center" label="" min-width="100" prop="devName"/>
            <el-table-column align="center" label="" min-width="150" prop="siteName"/>
            <el-table-column align="center" label="IP" min-width="120" prop="ip"/>
            <el-table-column align="center" label="MAC" min-width="160" prop="mac"/>
            <el-table-column align="center" label="" min-width="100" prop="typeName"/>
            <el-table-column align="center" label="" min-width="100">
              <template slot-scope="scope">
                <div v-if="scope.row.state === 1"><el-tag size="small"></el-tag></div>
                <div v-else><el-tag type="danger" size="small"></el-tag></div>
              </template>
            </el-table-column>
            <el-table-column align="center" label="" min-width="100" prop="remarks"/>
            <!--              -->
            <el-table-column fixed="right" width="170" align="right">
              <template slot="header" slot-scope="scope">
                <el-input v-model="search" size="medium" clearable @clear='UpDevData' @input="getSearch" placeholder=""/>
              </template>
              <template v-slot="scope">
                <el-button size="medium" type="warning" @click="editDevData(scope.row.devNumber) "></el-button>
                <el-button size="medium" type="danger" @click="deleteDevDate(scope.row.devId)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <!--  -->
          <el-pagination
                  layout="total, sizes, prev, pager, next, jumper" style="text-align: center;"
                  :current-page.sync="currentPage" @size-change="sizeChange" @current-change="currentChange" :total="totalCount">
          </el-pagination>
        </div>
      </div>
      <!-- -->
        <el-dialog title="" :visible.sync="dialogFormVisible" width="40%">
        <el-form ref="form" :model="form" label-width="120px">
          <el-form-item label="ID">
            <el-select v-model="form.siteId" placeholder="">
              <el-option v-for="item in store.siteAllData" :key="item.siteId" :label="item.siteName" :value="item.siteId"/>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="devNumber">
            <el-input v-model="form.devNumber" style="width: 300px"/>
          </el-form-item>
          <el-form-item label="" prop="devName">
            <el-input v-model="form.devName" style="width: 300px"/>
          </el-form-item>
          <el-form-item label="IP">
            <el-input v-model="form.ip" style="width: 300px"/>
          </el-form-item>
          <el-form-item label="MAC" prop="mac">
            <el-input v-model="form.mac" style="width: 300px"/>
          </el-form-item>
          <el-form-item label="(Id)" prop="typeId">
            <el-select v-model="form.typeId" placeholder="">
              <el-option v-for="item in store.deviceTypeAllData" :key="item.id" :label="item.typeName" :value="item.id"/>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="form.remarks" style="width: 300px"/>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="addDev()"></el-button>
            <el-button @click="dialogFormVisible = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
      <!--        -->
      <el-dialog title="" :visible.sync="dialogChangeDevFormVisible">
        <el-form ref="form" :model="devForm" label-width="80px">
          <el-form-item label="">
            <el-input v-model="devForm.devNumber" />
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="devForm.devName"/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="devForm.siteName" />
          </el-form-item>
          <el-form-item label="IP">
            <el-input v-model="devForm.ip"/>
          </el-form-item>
          <el-form-item label="MAC">
            <el-input v-model="devForm.mac"/>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="devForm.typeName" />
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="devForm.remarks"/>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitDevData()"></el-button>
            <el-button @click="dialogChangeVisible = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>
</template>

<script>
import {
  getCurrentDeviceServeData, addDeviceServeData, deleteDeviceServeData,
  updateDeviceServeData, searchDeviceServeData,searchDeviceStateServeData} from "@/network/device";
import {askState} from "@/network/beat";
import {store} from "@/store/store";

export default {
  name: 'DeviceMana',
  inject:['reload'],
  data () {
    return {
      store,
      search: '',
      getOrSearch:0,
      currentPage:1,
      state:'',
      intervalOn:null,
      pageNum:'1',
      pageSize:'10',
      totalCount:0,
      devForm: {}, //
      deviceData: [], //
      deviceSearchData: [], //
      PageName: '',
      dialogFormVisible: false, // 
      dialogChangeDevFormVisible: false,  // 
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      searchForm:{
        currentPage:1,
        pageSize:10,
        devNumber:'',
        devName:'',
        siteName:'',
        typeName:'',
      },
      form: {
        siteId:'',
        devNumber: '',
        devName: '',
        ip: '',
        mac: '',
        typeId: '',
        remarks: ''
      },
    }
  },

  created () {
    this.UpDevData()
    this.lunXun()
  },

  methods: {
    /**
     * 
     */
    UpDevData () {
      getCurrentDeviceServeData(this.pageNum,this.pageSize).then(res => {
        console.log(res.data.rows)
        this.deviceData = res.data.rows
        this.totalCount = res.data.totalCount
        this.getOrSearch = 0
      }).catch(err => {
        console.log(err)
      })
    },

    /**
     *  
     */
    currentChange (val) {
      if (this.getOrSearch === 0) {
        this.pageNum = val
        getCurrentDeviceServeData(this.pageNum,this.pageSize).then(res => {
          this.deviceData = res.data.rows
        }).catch(err => {
          console.log(err)
        })
      } else {
        this.searchForm.currentPage = val
        this.getQuery()
      }
    },

    /**
     * 
     */
    sizeChange (val) {
      if (this.getOrSearch === 0) {
        this.pageSize = val
        this.searchForm.pageSize = val
        getCurrentDeviceServeData(this.pageNum,this.pageSize).then(res => {
          this.deviceData = res.data.rows
        }).catch(err => {
          console.log(err)
        })
      } else {
        this.searchForm.pageSize = val
        this.getQuery()
      }
    },

    /**
     * 
     */
    addDev () {
      addDeviceServeData(this.form).then(res => {
        this.reload()
      }).catch(err => {
        console.log(err)
        alert(''+ err.code)
      })
      this.dialogFormVisible = false
    },

    /**
     * 
     * @param devNum 
     */
    editDevData (devNum) {
      this.dialogChangeDevFormVisible = true
      this.deviceData.map((item) => {
        if (devNum === item.devNumber) {
          const c = JSON.parse(JSON.stringify(item))
          this.devForm = c
        }
      })
    },

    /**
     * 
     */
    submitDevData () {
      updateDeviceServeData(this.devForm).then(res => {
        this.reload()
      }).catch(err => {
        console.log(err)
      })
      this.dialogChangeDevFormVisible = false
    },

    /**
     * 
     * @param id id
     */
    deleteDevDate (id) {
      deleteDeviceServeData(id).then(res => {
        this.reload()
      }).catch(err => {
        console.log('err',err)
      })
    },

    /**
     * 
     */
    getSearch () {
      window.clearInterval(this.intervalOn)
      let keyWord = this.search.toLowerCase()
      let arr = []
      //
      if (keyWord === '') {
        this.UpDevData()
      }
      arr = this.deviceData.filter(item => {
        if (item.devNumber.toLowerCase().indexOf(keyWord) !== -1) {
          return item
        }else if (item.devName.toLowerCase().indexOf(keyWord) !== -1) {
          return item
        }else if (item.siteName.toLowerCase().indexOf(keyWord) !== -1) {
          return item
        }else if (item.typeName.toLowerCase().indexOf(keyWord) !== -1) {
          return item
        }
      })
      this.totalCount = arr.length
      this.deviceData = arr
    },

    /**
     * 
     */
    getQuery () {
      window.clearInterval(this.intervalOn)
      if (this.getOrSearch === 0) {
        this.currentPage = 1
      }
      searchDeviceServeData(this.searchForm).then(res => {
        this.deviceSearchData = res.data.rows
        this.totalCount = res.data.totalCount
        this.getOrSearch = 1
      }).catch(err => {
        console.log(err)
      })
    },

    /**
     * 
     * @param id id
     */
    queryState (id) {
      id = Number(id)
      console.log(id)
      id = 2
      searchDeviceStateServeData(id).then(res => {
        console.log(res)
        this.UpDevData()
      }).catch(err => {
        console.log(err)
      })
    },

    /**
     * 
     */
    clearSearch () {
      this.intervalOn = window.setInterval( ()=>{this.askServe()},5e3)
      this.UpDevData()
      this.reload()
    },

    /**
     * 
     */
    askServe () {
      askState().then(res => {
        console.log(res)
        this.UpDevData()
      }).catch(err => {
        console.log(err)
      })
    },

    /**
     * 
     */
    lunXun () {
      this.intervalOn = window.setInterval( ()=>{this.askServe()},5e3)
    },
  },
}
</script>

<style scoped>
  .DevBody {
    display: flex;
    width: 100%;
  }

  .dev-item {
    width: 100%;
  }

  .dev-item-line {
    display: flex;
    margin-bottom: 10px;
    justify-content: space-between;
    width: 100%;
  }

  .dev-item-line-path {
    height: 40px;
    font-size: 18px;
    font-weight: 400;
    line-height: 40px;
    color: rgba(0,0,0,.85);
  }

  .dev-item-line-button {
    display: flex;
    justify-content: space-between;
  }

  .el-button--primary /deep/ .el-icon-search {
    margin-right: 5px;
  }

  .dev-item-line-button-input {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 850px;
    margin-right: 50px;
  }

  .input-style {
    padding-top: 3px;
    width: 150px;
  }

  .add-margin {
    margin-left: 10px;
  }

  .adjustment {
    margin-top: 4px;
  }
  .dev-item-table {
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  }

  .el-dialog {
    left: 700px;
    top: 200px;
    padding: 5px 50px;
    width: 400px;
    position: absolute;
    background-color: white;
    box-shadow: 0 2px 20px 1px rgb(128, 128, 128);
    z-index: 20;

  }
  .form .title {
    text-align: center;
  }

  .off-line {

  }

</style>
