<template>
  <div class="SiteBody">
    <div class="site-item">
      <div class="site-item-line">
        <div class="site-item-line-path">
          {{PageName}}
        </div>
        <div class="site-item-line-button">
          <div class="site-item-line-button-input">
            <el-select class="adjustment" clearable style="width: 150px" v-model="searchForm.name" placeholder="">
              <el-option v-for="(item,index) in this.store.regionAllData" :key="item.id" :label="item.name" :value="item.name"/>
            </el-select>
            <el-input class="input-style" clearable v-model="searchForm.siteName" size="medium" placeholder=""/>
            <el-input class="input-style" clearable v-model="searchForm.siteNumber" size="medium" placeholder=""/>
            <el-button class="add-margin" type="primary" icon="el-icon-search" @click="getQuery"></el-button>
            <el-button style="width: 80px" type="info" @click="clearSearch()"></el-button>
          </div>
          <el-button type="primary" @click="clickAddSite"></el-button>
        </div>
      </div>
      <!--  -->
      <div class="site-item-table">
        <el-table
          :data="getOrSearch === 0? this.store.siteData:siteSearchData"
          style="width: 100%">
          <el-table-column align="center" label="" min-width="100" prop="siteNumber"/>
          <el-table-column align="center" label="" min-width="100">
            <template v-slot="scope">
              <div>{{scope.row.siteName}}</div>
            </template>
          </el-table-column>
          <el-table-column align="center" label="GPS" min-width="200">
            <template v-slot="scope">
              <div>{{scope.row.siteRange[0]}},{{scope.row.siteRange[1]}}</div>
            </template>
          </el-table-column>
          <el-table-column align="center" label="" min-width="200">
            <template v-slot="scope">
              <div>{{scope.row.name}}</div>
            </template>
          </el-table-column>
          <el-table-column align="center" label="" min-width="100" prop="remarks"/>
          <!--  -->
          <el-table-column fixed="right" width="200" align="right">
            <template slot="header" slot-scope="scope">
              <el-input v-model="search" size="medium" clearable @clear='getSiteDataList' @input="getSearch" placeholder=""/>
            </template>
            <template v-slot="scope">
              <el-button size="medium" type="warning" @click="editSiteData(scope.row.siteId)"></el-button>&nbsp;&nbsp;&nbsp;
              <el-button size="medium" type="danger" @click="deleteSite(scope.row.siteId)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination
                layout="total, sizes, prev, pager, next, jumper" style="text-align: center;"
                :current-page.sync="currentPage" @size-change="sizeChange" @current-change="currentChange" :total="totalCount">
        </el-pagination>
      </div>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="id" prop="rgId">
          <el-select v-model="form.rgId" placeholder="">
            <el-option v-for="item in store.regionAllData" :key="item.id" :label="item.name" :value="item.id"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="siteNumber">
          <el-input v-model="form.siteNumber"/>
        </el-form-item>
        <el-form-item label="" prop="siteName">
          <el-input v-model="form.siteName"/>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="form.remarks"/>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addSite()"></el-button>
          <el-button @click="cancelChange"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogChangeVisible">
      <el-form ref="form" :model="SiteForm" label-width="80px">
        <el-form-item label="">
          <el-input v-model="SiteForm.siteNumber" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="SiteForm.siteName"/>
        </el-form-item>
        <el-form-item label="GPS">
          <el-input v-model="SiteForm.siteRange" disabled/>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="SiteForm.name" />
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="SiteForm.remarks"/>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitSiteData()"></el-button>
          <el-button @click="dialogChangeVisible = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import {
  getCurrentSiteServeData,
  updateSiteServeData,
  addSiteServeData,
  deleteSiteServeData,
  searchSiteServeData} from "@/network/site";
import {store} from "@/store/store";
import qs from "qs";

export default {
  name: 'SiteMana',
  inject:['reload'],
  data () {
    return {
      store,
      search: '',
      getOrSearch:0,
      siteSearchData:[],
      currentPage:1,
      pageNum:'1',
      pageSize:'10',
      totalCount:0,
      number1: null, // 
      number2: null,  // 
      PageName: '',
      SiteForm: {}, // 
      SiteData: [], // 
      dialogFormVisible: false, // 
      dialogChangeVisible: false, // 
      searchForm:{
        currentPage:1,
        pageSize:10,
        name:'',
        siteName:'',
        siteNumber:'',
      },
      rules:{
        rgId:[
          {required: true,message: '',trigger: 'change'}
        ],
        siteNumber:[
          {required: true,message: '',trigger: 'blur'}
        ],
        siteName: [
          {required: true,message: '',trigger: 'blur'}
        ],
      },
      // 
      form: {
        name: '',
        siteName: '',
        siteNumber: '',
        siteRange: [],
        rgId: '',
        remarks: '',
      },
      defaultProps: {  // 
        children: 'children',
        label: 'label'
      },
    }
  },

  mounted () {
    if (this.$route.params.order === '') {
      console.log('gps1',this.store.gps)
      console.log(this.$route.params.order)
      this.openAddForm()
    }

    if (this.$route.params.siteID >= 0) {
      let c = Number(this.$route.params.siteID)
      this.openChangeForm(c)
    }

  },

  created () {
    this.getSiteDataList()
  },

  methods: {
    testSite () {
      console.log(this.SiteForm)
    },

    /**
     * 
     */
    getSiteDataList () {
      getCurrentSiteServeData(this.pageNum,this.pageSize).then(res => {
        console.log("res1" , res.data.rows)
        this.store.siteData.splice(0)
        this.store.siteData = res.data.rows
        this.totalCount = res.data.totalCount
        this.getOrSearch = 0
        this.getToArray(this.store.siteData)
      }).catch(err => {
        console.log(err)
      })
    },

    /**
     * 
     * @param obj 
     */
    getToArray (obj) {
      for (let i = 0; i < obj.length; i++) {
        let c = JSON.parse(obj[i].siteRange)
        obj[i].siteRange = c
      }
    },

    /**
     * 
     * @param val - 
     */
    currentChange (val) {
      if (this.getOrSearch === 0) {
        this.pageNum = val
        getCurrentSiteServeData(this.pageNum,this.pageSize).then(res => {
          this.store.siteData.splice(0)
          this.store.siteData = res.data.rows
          console.log(res.data.rows)
          this.getToArray(this.store.siteData)
        }).catch(err => {
          console.log(err)
        })
      } else {
        this.searchForm.currentPage = val
        this.getQuery()
      }
    },

    /**
     * 
     * @param val - 
     */
    sizeChange (val) {
      if (this.getOrSearch === 0) {
        this.pageSize = val
        this.searchForm.pageSize = val
        getCurrentSiteServeData(this.pageNum,this.pageSize).then(res => {
          console.log("res " , res.data)
          this.store.siteData.splice(0)
          this.store.siteData = res.data.rows
          this.getToArray(this.store.siteData)
        }).catch(err => {
          console.log(err)
        })
      } else {
        this.searchForm.pageSize = val
        this.getQuery()
      }
    },

    /**
     * 
     * this.dialogFormVisible - 
     */
    clickAddSite () {
      this.$router.push({
        path:'/siteMap',
        query:{
          order:'',
        }
      })
    },

    /**
     * 
     */
    openAddForm () {
      this.dialogFormVisible = true
    },

    /**
     * 
     */
    addSite () {
      this.form.siteRange = this.store.gps
      console.log('siteRange', this.form.siteRange)
      this.form.siteRange = JSON.stringify(this.form.siteRange)
      addSiteServeData(this.form).then(res => {
        console.log(res)
        window.location.reload()
      }).catch(err => {
        console.log(err)
      })
      this.dialogFormVisible = false
    },

    /**
     * 
     * @param  siteId - id
     */
    editSiteData (siteId) {
      console.log('st',siteId)
      console.log(typeof siteId)
      this.$router.push({
        path:'/siteMap',
          query:{
            siteId:siteId,
          }
      })
    },

    /**
     * 
     */
    openChangeForm (siteId) {
      this.dialogChangeVisible = true
      for (let i = 0; i < this.store.siteData.length; i++) {
        if (siteId === this.store.siteData[i].siteId) {
          const c = JSON.parse(JSON.stringify(this.store.siteData[i]))
          this.SiteForm = c
          console.log(this.SiteForm)
        }
      }
    },

    /**
     *
     */
    submitSiteData () {
      this.SiteForm.siteRange = JSON.stringify(this.SiteForm.siteRange)
      let c = qs.stringify(this.SiteForm)
      updateSiteServeData(c).then(res => {
        console.log(res)
        window.location.reload()  // 
      }).catch(err => {
        console.log(err)
      })
      this.dialogChangeVisible = false
    },

    /**
     * 
     * @param id id
     */
    deleteSite (id) {
      deleteSiteServeData(id).then(res => {
        console.log('res',res)
        this.reload()
      }).catch(err => {
        console.log('err',err)
      })
    },

    /**
     * 
     */
    getSearch () {
      let keyWord = this.search.toLowerCase()
      let arr = []
      //
      if (keyWord === '') {
        this.getSiteDataList()
      }
      arr = this.store.siteData.filter(item => {
        if (item.siteNumber.toLowerCase().indexOf(keyWord) !== -1) {
          return item
        }else if (item.siteName.toLowerCase().indexOf(keyWord) !== -1) {
          return item
        }else if (item.name.toLowerCase().indexOf(keyWord) !== -1) {
          return item
        }
      })
      this.totalCount = arr.length
      this.store.siteData = arr
    },

    /**
     * 
     */
    getQuery () {
      if (this.getOrSearch === 0) {
        this.currentPage = 1
      }
      searchSiteServeData(this.searchForm).then(res => {
        this.siteSearchData = res.data.rows
        this.totalCount = res.data.totalCount
        this.getToArray(this.siteSearchData)
        this.getOrSearch = 1
      }).catch(err => {
        console.log(err)
      })
    },

    /**
     * 
     */
    clearSearch () {
      this.getSiteDataList()
      this.reload()
    },

    /**
     * 
     */
    cancelChange () {
      this.dialogFormVisible = false
      window.location.reload()
    },
  },


}
</script>

<style scoped>
  .SiteBody {
    display: flex;
    width: 100%;
  }

  .site-item {
    width: 100%;
  }

  .site-item-line {
    display: flex;
    margin-bottom: 10px;
    justify-content: space-between;
    width: 100%;
  }

  .site-item-line-path {
    height: 40px;
    font-size: 18px;
    font-weight: 400;
    line-height: 40px;
    color: rgba(0,0,0,.85);
  }

  .site-item-line-button {
    display: flex;
    justify-content: space-between;
  }

  .el-button--primary /deep/ .el-icon-search {
    margin-right: 5px;
  }

  .site-item-line-button-input {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 750px;
    margin-right: 50px;
  }

  .input-style {
    padding-top: 3px;
    width: 150px;
  }

  .add-margin {
    margin-left: 10px;
  }

  .site-item-table {
    width: 100%;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  }

  .adjustment {
    margin-top: 4px;
  }

  .el-dialog {
    left: 700px;
    top: 200px;
    padding: 5px 50px;
    width: 400px;
    position: absolute;
    background-color: white;
    box-shadow: 0 2px 20px 1px rgb(128, 128, 128);
    z-index: 20;
  }

  .form .title {
    text-align: center;
  }
</style>
