<template>
  <div>
    <div id="map"></div>
    <div class="item">
      <h4></h4>
      <div class="input-item">
        <input type="text" readonly="true" v-model="gps">
      </div>
    </div>
    <!--  -->
    <div class="change-draw" v-show="showChangeDraw">
      <el-row>
        <el-button size="medium" type="primary" @click="cancelChangeDraw"></el-button>
        <el-button size="medium" type="primary" @click="polyEditor.open()"></el-button>
        <el-button size="medium" type="primary" @click="enterChange"></el-button>
      </el-row>
    </div>
    <!--          -->
    <el-dialog title="" :visible.sync="dialogRegVisible">
      <el-form ref="form" :model="RegForm" label-width="80px">
        <el-form-item label="">
          <el-input v-model="RegForm.id" disabled/>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="RegForm.name"/>
        </el-form-item>

        <el-form-item label="">
          <el-select v-model="RegForm.riskLevel" placeholder="">
            <el-option v-for="item in riskLevelOptions" :key="item.value" :label="item.label" :value="item.value"/>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="RegForm.controlLevel" placeholder="">
            <el-option v-for="item in controlLevelOptions" :key="item.value" :label="item.label" :value="item.value"/>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="RegForm.remarks"/>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitRegData"></el-button>
          <el-button @click="cancelChangeForm"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import AMapLoader from '@amap/amap-jsapi-loader'
import {store} from "@/store/store";
import bus from "@/components/bus";
import qs from "qs";
import {updateRegionServeData} from "@/network/region";

export default {
  name: 'Map',
  inject:['reload'],
  data () {
    return {
      store,
      gps: '',
      newMap:'',
      siteIdT:'',
      nameList:[],
      RegForm: {},
      polygon:{},
      polyEditor:{},
      dialogRegVisible: false,
      showChangeDraw:false,
      riskLevelOptions: [
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        }
      ],
      controlLevelOptions: [
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        }
      ],
      polygonList:[{},{},{}],   // 
      markerList:[[],[],[]],  // 
      drawPolygonPath:[],   // 
    }
  },

  mounted () {
    this.initMap()
  },

  methods: {
    /**
     * 
     */
    initMap () {
      AMapLoader.load({
        key: 'e0182f82d3a2e2470ca386ea85595acc', // WebKey load 
        version: '2.0', //  JSAPI  1.4.15
        plugins: ['AMap.AutoComplete', 'AMap.ElasticMarker', 'AMap.TileLayer', 'AMap.PlaceSearch', 'AMap.Scale',"AMap.MarkerCluster",
          'AMap.OverView', 'AMap.ToolBar','AMap.MouseTool', 'AMap.MapType','AMap.Driving','AMap.DragRoute', 'AMap.PolygonEditor',
          'AMap.CircleEditor', 'AMap.ControlBar'] // 'AMap.Scale'
      }).then((AMap) => {
        this.newMap = new AMap.Map('map', {
          resizeEnable: true,
          zoom: 15,
          viewMode: '3D',
          center: [114.215448,22.684815]
        })
        // 
        this.newMap.on('click', (e) => {
          this.gps = e.lnglat.lng + ',' + e.lnglat.lat
          console.log(this.gps)
        })
        // 
        this.createDefaultPolygon()
        console.log('6610')
        this.addAllPolygonToMap()
        this.addMarker()
        // 
        if (this.$route.query.id >= 0) {
          console.log('45615')
          this.showChangeDraw = true
          console.log('45615')
          this.changeDraw(this.$route.query.id)
        }
        let infoWindow = new AMap.InfoWindow({offset: new AMap.Pixel(0, -30)});
        // window.setInterval( ()=>{this.askServe()},5000)
      }).catch(e => {
        console.log(e)
      })
    },

    /**
     * 
     */
    testFunc () {
      let c = new Array()
      c[0] = new Array()
      c[0][0] = 1
      console.log(c)
    },

    /**
     * 
     */
    createDefaultPolygon () {
      //
      this.polygonList.splice(0)
      this.store.getRegAllData()
      for (let i=0 ; i<this.store.regionAllData.length ; i++) {
        switch (this.store.regionAllData[i].riskLevel) {
          case '':
            this.polygonList[i] = new AMap.Polygon({
            path: this.store.regionAllData[i].area,          // 
            fillColor: '#7FFFD4',
            strokeOpacity: 1,
            fillOpacity: 0.7,
            strokeColor: '#7FFFD4',
            strokeWeight: 2,
            strokeStyle: 'solid',
            strokeDasharray: [5, 5],
          });
            break;
          case '':
            this.polygonList[i] = new AMap.Polygon({
              path: this.store.regionAllData[i].area,          // 
              fillColor: '#00BFFF',
              strokeOpacity: 1,
              fillOpacity: 0.7,
              strokeColor: '#00BFFF',
              strokeWeight: 2,
              strokeStyle: 'solid',
              strokeDasharray: [5, 5],
            });
            break;
          case '':
            this.polygonList[i] = new AMap.Polygon({
              path: this.store.regionAllData[i].area,          // 
              fillColor: '#FFD700',
              strokeOpacity: 1,
              fillOpacity: 0.7,
              strokeColor: '#FFD700',
              strokeWeight: 2,
              strokeStyle: 'solid',
              strokeDasharray: [5, 5],
            });
            break;
          case '':
            this.polygonList[i] = new AMap.Polygon({
              path: this.store.regionAllData[i].area,          // 
              fillColor: '#FF0000',
              strokeOpacity: 1,
              fillOpacity: 0.7,
              strokeColor: '#FF0000',
              strokeWeight: 2,
              strokeStyle: 'solid',
              strokeDasharray: [5, 5],
            });
            break;
          default:
            this.polygonList[i] = new AMap.Polygon({
              path: this.store.regionAllData[i].area,          // 
              fillColor: '#7FFFD4',
              strokeOpacity: 1,
              fillOpacity: 0.7,
              strokeColor: '#7FFFD4',
              strokeWeight: 3,
              strokeStyle: 'solid',
              strokeDasharray: [5, 5],
            });
        }
      }
    },

    /**
     * 
     * @param polygon - 
     * @param zoom - number
     */
    addPolygonToMap (polygon,zoom) {
      // 
      this.newMap.remove(this.polygonList)
      this.newMap.add(this.polygonList[polygon]);
      // 
      this.newMap.setCenter(this.store.regionAllData[polygon].area[0])
      // 
      let zoom1 = Number(this.store.regionAllData[polygon].zoom)
      this.newMap.setZoom(zoom1)
    },

    /**
     * 
     */
    addAllPolygonToMap () {
      // 
      this.newMap.remove(this.polygonList)
      this.newMap.add(this.polygonList);
      console.log(this.polygonList.length)
    },

    /**
     * 
     */
    drawPolygon () {
      let mouseTool = new AMap.MouseTool(this.newMap)
      mouseTool.polygon({
        strokeColor: "#FF33FF",
        strokeOpacity: 1,
        strokeWeight: 6,
        strokeOpacity: 0.2,
        fillColor: '#1791fc',
        fillOpacity: 0.4,
        strokeStyle: "solid",
      })
      // 
      this.drawPolygonPath.splice(0)
      // 
      mouseTool.on('draw', (e) => {
        // event.obj 
        // 
        // zoom number
        let regZoom = this.newMap.getZoom();
        this.$parent.addRegForm.zoom = Math.floor(regZoom)
        // e.obj._opts.path 
        let d  = JSON.stringify(e.obj._opts.path)  // 
        let e1 =  '{"area":"' + d + '"}'
        this.$parent.addRegForm.area = e1 // 
      })
      return this.drawPolygonPath
    },

    /**
     * 
     */
    changeDraw (id,index) {
      let arr = []
      let ct = 1
      let myId = Number(id)
      for (let i = 0; i < this.store.regionData.length; i++) {
        if (myId === this.store.regionData[i].id) {
          arr = this.store.regionData[i].area
          ct = i
        }
      }
      console.log('c',arr)
      this.polygon = new AMap.Polygon({
        path: arr,
        strokeColor: "#FF33FF",
        strokeWeight: 6,
        strokeOpacity: 0.2,
        fillOpacity: 0.4,
        fillColor: '#1791fc',
        zIndex: 50,
        bubble: true,
      })
      this.newMap.add(this.polygon)
      // 
      this.newMap.setCenter(this.store.regionData[ct].area[0])
      // 
      let zoom1 = Number(this.store.regionData[ct].zoom)
      this.newMap.setZoom(zoom1)
      // 
      this.drawPolygonPath.splice(0)
      this.searchForChange(myId)
      this.polyEditor = new AMap.PolygonEditor(this.newMap, this.polygon);
      return this.drawPolygonPath
    },

    /**
     * 
     * mathZoom getZoom
     */
    enterChange () {
      this.polyEditor.close()
      let c = JSON.stringify(this.polygon._opts.path)  // 
      let e =  '{"area":"' + c + '"}'
      this.RegForm.area = e
      let mathZoom = this.newMap.getZoom();
      this.RegForm.zoom = Math.floor(mathZoom)
      console.log(this.RegForm)
      console.log(this.RegForm.zoom)
      this.showChangeDraw = false
      this.dialogRegVisible = true
    },

    /**
     * 
     */
    searchForChange (id) {
      for (let i = 0; i < this.store.regionData.length; i++) {
        if (id === this.store.regionData[i].id) {
          let c = JSON.parse(JSON.stringify(this.store.regionData[i]))
          this.RegForm = c
        }
      }
    },

    /**
     * 
     */
    submitRegData (num) {
      let c = qs.stringify(this.RegForm)
      updateRegionServeData(c).then(res => {
        console.log('zl1',res)
        this.reload()
      }).catch(err => {
        console.log(err)
      })
      this.dialogRegVisible = false
      this.showChangeDraw = false
      this.$router.push('/RegMana/RegManaList')
    },

    /**
     * 
     */
    cancelChangeDraw () {
      this.showChangeDraw = false
      this.reload()
      this.$router.push('/RegMana/RegManaList')
    },

    /**
     * 
     */
    addMarker () {
      for (let i=0 ; i<this.store.siteAllData.length ; i++) {
        this.markerList[i] = new Array()
        this.markerList[i][0] = this.store.siteAllData[i].siteName
        this.markerList[i][1] = new AMap.Marker({
          position: this.store.siteAllData[i].siteRange,
          offset: new AMap.Pixel(-13, -30),
          label:{
            direction:'right',
            offset:new AMap.Pixel(10, 0),  //
            content:this.store.siteAllData[i].siteName,
          },
        });
      }
      this.addAllMarkerToMap()
    },

    /**
     * 
     * @param num
     */
    addAllMarkerToMap (num) {
      for (let i=0 ; i<this.markerList.length ; i++) {
        this.markerList[i][1].setMap(this.newMap)
      }
    },

    /**
     * 
     * @param num
     */
    addMarkerToMap (name) {
      // 
      console.log(name)
      let c = 0
      this.newMap.remove(this.markerList)
      this.markerList.map((item,index) => {
        if (item[0] === name) {
          this.newMap.add(item[1])
          c = index
        }
      })
      this.newMap.setCenter(this.store.siteAllData[c].siteRange)
    },

    /**
     * 
     */
    askServe () {
      this.store.getAllVehicleData()
      let nowDate = +new Date()
      let index = this.store.vehicleAllData.length
      let item = this.store.vehicleAllData[index-1]
      let itemDate = item.startTime
      let aDate = +new Date(itemDate)
      console.log('seq', typeof item.siteId)

      if (nowDate - aDate <= 600000) {
        this.siteIdT = item.siteId
        this.store.siteAllData.map((item,index) => {
          if (item.siteId === id) {
          }
        })
      }
    },

    cancelChangeForm () {
      this.dialogChangeVisible = false
      this.$router.push('/RegMana/RegManaList')
    },

  },
}
</script>

<style scoped>
  #map {
    margin: 1px 1px;
    padding: 1px 1px;
    width: 100%;
    height: 700px;
    border: 1px none red;
  }

  .item {
    position: absolute;
    padding-top: 5px;
    padding-left: 10px;
    right: 50px;
    top: 700px;
    border-radius:10px;
    width: 180px;
    height: 50px;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  }

  .item h4 {
    font-family: "", "Microsoft YaHei", "", Arial, sans-serif;
  }

  .change-draw {
    position: absolute;
    top: 150px;
    right: 100px;
    padding-top: 10px;
    display: flex;
    justify-content: center;
    height: 45px;
    width: 300px;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  }

  .amap-marker-label {
    border-style: none;
  }
</style>
