import {request} from "@/network/request";

/**
 * 
 */

/**
 * 
 */
export function getSiteServeData () {
  return request({
    url:'/site/selectAll',
    method:'get'
  })
}

/**
 * 
 * @param pageNum 
 * @param pageSize 
 */
export function getCurrentSiteServeData (pageNum,pageSize) {
  return request({
    url:'/site/selectAll',
    method:'get',
    params:{
      currentPage:pageNum,
      pageSize:pageSize,
    }
  })
}

/**
 * 
 */
export function addSiteServeData (obj) {
  return request({
    url:'/site/add',
    method:'post',
    data:obj,
    headers:{
      'Content-Type':'multipart/form-data'
    },
  })
}

/**
 * 
 */
export function deleteSiteServeData (id) {
  return request({
    url:`/site/${id}`,
    method:'delete',
  })
}

/**
 * 
 */
export function updateSiteServeData (obj) {
  return request({
    url:'/site/update',
    method:'put',
    data:obj,
    headers:{
      'Content-Type':'application/x-www-form-urlencoded'
    },
  })
}

/**
 * 
 */
export function searchSiteServeData (obj) {
  return request({
    url:'/site/selectCondition',
    method:'get',
    params:obj
  })
}