<template>
  <div class="reg-body">
    <div class="reg-body-total">
      <div class="reg-body-item-select">
        <el-select style="margin-right: 10px" clearable @clear="emptySelect" v-model="value1" placeholder="" :disabled="select">
          <el-option v-for="(item,index) in this.store.regionAllData" :key="item.id" :label="item.name" :value="item.id" @click.native="showRegional(index,item.id)"/>
        </el-select>
        <el-select v-if="allOrPart===1" style="margin-right: 10px" v-model="value2" placeholder="" :disabled="select">
          <el-option v-for="(item,index) in this.store.siteAllData" :key="index" :label="item.siteName" :value="item.siteName" @click.native="showMarker(item.siteName)"/>
        </el-select>
        <el-select v-else style="margin-right: 10px" v-model="value2" placeholder="" :disabled="select">
          <el-option v-for="(item,index) in this.sitePartData" :key="index" :label="item.siteName" :value="item.siteName" @click.native="showMarker(item.siteName)"/>
        </el-select>
      </div>
      <div class="Rebody-item-click">
        <div class="Rebody-item-button">
          <el-button type="primary" @click="addRegion" :disabled="select"></el-button>
        </div>
        <div class="Rebody-item-button">
          <el-button type="success" @click="clickRegManaList"></el-button>
        </div>
        <div class="Rebody-item-button">
          <el-button type="success" @click="clickMap">Map</el-button>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="routerStyle">
      <router-view ref="child"></router-view>
    </div>
    <!--  -->
    <div class="draw" v-show="showDraw">
      <div class="word">
        
      </div>
      <el-row>
        <el-button size="medium" type="primary" style="margin-bottom: 7px" @click="cancelDraw"></el-button>
        <el-button size="medium" type="primary" @click="submitDrawPath"></el-button>
      </el-row>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogAddRegVisible">
      <el-form ref="form" :model="addRegForm" label-width="80px">
        <el-form-item label="">
          <el-input v-model="addRegForm.name"/>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="addRegForm.riskLevel" placeholder="">
            <el-option v-for="item in riskLevelOptions" :key="item.value" :label="item.label" :value="item.value"/>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="addRegForm.controlLevel" placeholder="">
            <el-option v-for="item in controlLevelOptions" :key="item.value" :label="item.label" :value="item.value"/>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="addRegForm.remarks"/>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="addRegionToServe"></el-button>
          <el-button @click="dialogAddRegVisible = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import Map from '@/views/QuYu/Map'
import { store } from '@/store/store'
import RegManaList from '@/views/QuYu/RegManaList'
import {getCurrentRegionServeData,addRegionServeData,getSiteName} from "@/network/region";
import qs from "qs";

// 
export default {
  name: 'RegionalMana',
  inject:['reload'],
  components: {
    Map,
    RegManaList
  },
  data () {
    return {
      store,
      select:false, // 
      allOrPart:1,
      value1: '',
      value2: '',
      value3: '',
      pageNum: '1',
      pageSize:'10',
      showDraw:false,
      regionData:[],
      siteAllData:[],
      sitePartData:[],
      pageName: '',
      dialogAddRegVisible: false,
      riskLevelOptions: [
        {
          value: '',
          label: ''
        },
        {
        value: '',
        label: ''
        },
        {
        value: '',
        label: ''
        },
        {
        value: '',
        label: ''
        }
      ],
      controlLevelOptions: [
        {
        value: '',
        label: ''
        },
        {
        value: '',
        label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        }
      ],
      addRegForm: {
        name: '',
        area: [],
        riskLevel:'',
        controlLevel:'',
        remarks: '',
        zoom:15,
      },
    }
  },

  mounted() {
    this.decideSelect()
    this.store.getSiteAllDataList()
  },

  methods: {
    /**
     * 
     */
    clickMap () {
      this.$router.push('/RegMana/Map')
      window.location.reload()
      this.select = false
    },

    /**
     * 
     */
    clickRegManaList () {
      this.$router.push('/RegMana/RegManaList')
      this.select = true
    },

    /**
     * 
     */
    decideSelect () {
      if (this.$router.history.current.path === '/RegMana/RegManaList') {
        this.select = true
      }
    },

    /**
     * 
     */
    getToArray () {
      for (let i = 0; i < this.store.regionData.length; i++) {
        let c = JSON.parse(this.store.regionData[i].area)
        this.store.regionData[i].area = c
      }
    },

    /**
     * 
     */
    addRegion () {
      this.showDraw = true
      this.addRegForm.area = this.$refs.child.drawPolygon()
    },

    /**
     * 
     */
    submitDrawPath () {
      this.showDraw = false
      this.dialogAddRegVisible = true // 
    },

    /**
     * 
     */
    addRegionToServe () {
      addRegionServeData(this.addRegForm).then(res => {
        console.log(res)
        this.$refs.child.createDefaultPolygon()
        this.$refs.child.addAllPolygonToMap()
        this.dialogAddRegVisible = false
      }).catch(err => {
        console.log(err)
        alert(''+ err.code)
      })
    },

    /**
     * 
     */
    showRegional (num,id) {
      let num1 = Number(num)
      getSiteName(id).then(res => {
        this.sitePartData = res.data
        this.allOrPart = 2
      }).catch(err => {
        console.log(err)
      })
      this.$refs.child.addPolygonToMap(num1)
    },

    /**
     * 
     */
    showMarker (name) {
      this.$refs.child.addMarkerToMap(name)
    },

    /**
     * 
     */
    cancelDraw () {
      this.reload()
    },

    /**
     * 
     */
    emptySelect () {
      this.allOrPart = 1
      this.$refs.child.addAllPolygonToMap()
    },
  }
}
</script>

<style scoped>
  .reg-body-total {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
  }

  .reg-body-item-select {
    display: flex;
    justify-content: space-around;
    width: 430px;
  }

  .Rebody-item-button {
    width: 100px;

  }

  .Rebody-item-click {
    display: flex;
    justify-content: flex-end;
    width: 300px;
    height: 40px;
    line-height: 40px;
    text-align: center;
  }

  .draw {
    position: absolute;
    top: 150px;
    left: 200px;
    text-align: center;
    padding-top: 5px;
    border-radius: 10px;
    background-color: rgba(255,255,255, .7);
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  }

  .draw .word {
    font-family: "FangSong", "Microsoft YaHei", "", Arial, sans-serif;
    font-weight: 600;
    color: #6495ED;
    margin-bottom: 5px;
    width: 290px;

  }
</style>
