import {request} from "@/network/request";

/**
 * 
 */

/**
 * 
 */
export function getAllVehicleServeData () {
  return request({
    url:'/vehicle/selectAll',
    method:'get',
    params:{
      currentPage:'1',
      pageSize:'10'
    }
  })
}

/**
 * 
 * @param pageNum 
 * @param pageSize 
 */
export function getCurrentVehicleServeData (pageNum,pageSize) {
  return request({
    url:'/vehicle/selectAll',
    method:'get',
    params:{
      currentPage:pageNum,
      pageSize:pageSize,
    }
  })
}

/**
 * 
 */
export function addVehicleServeData (obj) {
  return request({
    url:'/vehicle/add',
    method:'post',
    params:obj
  })
}



/**
 * 
 */
export function searchVehicleServeData (obj) {
  console.log('obj',obj)
  return request({
    url:'/vehicle/selectCondition',
    method:'get',
    params:obj
  })
}

/**
 * 
 */
export function getVehicleTrackServeData (obj) {
  return request({
    url:'/vehicle/judge',
    method:'post',
    params:obj
  })
}