<template>
  <div>
    <div id="container"></div>
    <!--  -->
    <div class="item">
      <h4></h4>
      <div class="input-item">
        <input type="text" readonly="true" v-model="this.store.gps">
      </div>
    </div>
    <!--  -->
    <div class="draw" v-show="showDraw">
      <div class="word"></div>
      <div class="button">
        <el-row>
          <el-button size="medium" type="primary" @click="addMarker"></el-button>
          <el-button size="medium" type="primary" @click="clickToList(str1)"></el-button>
          <el-button size="medium" type="primary" @click="cancel"></el-button>
        </el-row>
      </div>
    </div>
    <!--  -->
    <div class="change-site-draw" v-show="changeDraw">
      <div class="word">
        
      </div>
      <div class="">
        <el-row>
          <el-button size="medium" type="primary" @click="cancelChange"></el-button>
          <el-button size="medium" type="primary" @click="dragSite"></el-button>
          <el-button size="medium" type="primary" @click="endChange"></el-button>
        </el-row>
      </div>
    </div>
  </div>

</template>

<script>
import AMapLoader from '@amap/amap-jsapi-loader'
import {store} from "@/store/store";
import {addSiteServeData} from "@/network/site";

export default {
  name: "siteMap",
  data () {
    return {
      store,
      gps:[],
      str1:'',
      Marker:[],
      markerList:[],
      newSiteRange:[],
      showDraw:false,
      changeDraw:false,
      siteId:'',
      arr:[],
      i:0,
    }
  },

  mounted() {
    this.initMap()
  },

  methods: {
    initMap(){
      AMapLoader.load({
        key:"e0182f82d3a2e2470ca386ea85595acc",             // WebKey load 
        version:"2.0",      //  JSAPI  1.4.15
        plugins:["AMap.MarkerCluster","AMap.ElasticMarker"],       // 'AMap.Scale'
      }).then((AMap)=>{
        this.store.siteMap = new AMap.Map("container",{  //id
          resizeEnable: true,
          zoom: 15,
          viewMode: '3D',
          center: [114.215448,22.684815]
        });

        // 
        this.store.siteMap.on('click', (e) => {
          this.store.gps.splice(0)
          this.store.gps.push(e.lnglat.lng,e.lnglat.lat)
        })

        // 
        if (this.$route.query.order === '') {
          this.showDraw = true
        }
        // 
        if (this.$route.query.siteId >= 0) {
          this.searchSite(this.$route.query.siteId)
          this.siteId = this.$route.query.siteId
          this.changeDraw = true
          this.changedMarker(this.$route.query.siteId,this.arr)
        }

        this.addAllMarker()
      }).catch(e=>{
        console.log(e);
      })
    },

    /**
     * 
     */
    addMarker () {
      this.store.Marker = new AMap.Marker({
        icon: "//a.amap.com/jsapi_demos/static/demo-center/icons/poi-marker-default.png",
        position: this.store.gps,
        offset: new AMap.Pixel(-13, -30)
      })
      this.store.Marker.setMap(this.store.siteMap)
    },

    /**
     * 
     */
    clickToList (str) {
      this.showDraw = false
      this.$router.push({
        name:'SiteMana',
        params:{
          order:str,
        }
      })
    },

    /**
     * 
     */
    cancel () {
      this.showDraw = false
      this.$router.push('/SiteMana')
    },

    /**
     * 
     * @param siteId - id
     */
    changedMarker (siteId,arr) {
      this.Marker = new AMap.Marker({
        icon: "//a.amap.com/jsapi_demos/static/demo-center/icons/poi-marker-default.png",
        position: arr,
        offset: new AMap.Pixel(-13, -30)
      })
      this.newSiteRange = arr
      this.Marker.setMap(this.store.siteMap)
      this.store.siteMap.setCenter(arr)
    },

    /**
     * 
     */
    dragSite () {
      this.Marker.setDraggable(true)
      this.Marker.on('dragend',e => {
        this.newSiteRange.splice(0)
        this.newSiteRange.push(e.lnglat.lng,e.lnglat.lat)
      })
    },

    /**
     * 
     */
    endChange () {
      this.Marker.setDraggable(false)
      this.changeDraw = false
      this.store.siteData[this.i].siteRange = this.newSiteRange
      this.$router.push({
        name:'SiteMana',
        params:{
          siteID:this.siteId
        }
      })
    },

    /**
     * 
     * @param siteId - id
     */
    searchSite (siteId) {
      let myId = Number(siteId)
      console.log(this.store.siteAllData)
      for (let i = 0; i < this.store.siteAllData.length; i++) {
        console.log(this.store.siteAllData[i].siteId)
        if (myId === this.store.siteAllData[i].siteId) {
          this.arr = this.store.siteAllData[i].siteRange
          this.i = i
        }
      }
    },

    /**
     * 
     */
    cancelChange () {
      this.changeDraw = false
      this.$router.push('/SiteMana')
    },

    /**
     * 
     */
    addAllMarker () {
      for (let i=0 ; i<this.store.siteAllData.length ; i++) {
        this.markerList[i] = new AMap.Marker({
          position: this.store.siteAllData[i].siteRange,
          offset: new AMap.Pixel(-13, -30),
          label:{
            direction:'right',
            offset:new AMap.Pixel(10, 0),  //
            content:this.store.siteAllData[i].siteName,
          },
        });
      }
      this.addMarkerToMap()
    },

    /**
     * 
     * @param num
     */
    addMarkerToMap (num) {
      for (let i=0 ; i<this.markerList.length ; i++) {
        this.markerList[i].setMap(this.store.siteMap)
      }
    }
  },
}
</script>

<style scoped>
  #container {
    margin: 1px 1px;
    padding: 1px 1px;
    width: 100%;
    height: 700px;
    border: 1px none red;
  }

  .item {
    position: absolute;
    padding-top: 5px;
    padding-left: 10px;
    right: 50px;
    top: 700px;
    border-radius:10px;
    width: 180px;
    height: 50px;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  }

  .item h4 {
    font-family: "", "Microsoft YaHei", "", Arial, sans-serif;
  }

  .draw {
    position: absolute;
    text-align: center;
    top: 100px;
    left: 200px;
    padding-top: 10px;
    height: 65px;
    width: 400px;
    font-weight: 400;
    border-radius: 10px;
    background-color: rgba(255,255,255, .7);
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  }

  .draw .word {
    font-family: "FangSong", "Microsoft YaHei", "", Arial, sans-serif;
    font-weight: 600;
    color: #6495ED;
    margin-bottom: 5px;
  }

  .change-site-draw {
    position: absolute;
    top: 100px;
    right: 50px;
    padding: 5px;
    text-align: center;
    border-radius: 10px;
    background-color: rgba(255,255,255, .7);
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  }

  .change-site-draw .word {
    font-family: "FangSong", "Microsoft YaHei", "", Arial, sans-serif;
    font-weight: 600;
    color: #6495ED;
    margin-bottom: 5px;
    width: 290px;

  }

  .button {

  }
</style>