<template>
  <div class="type-body">
    <div class="type-line">
      <div class="type-line-path">{{PageName}}</div>
      <div class="type-line-button">
        <el-button type="primary" @click="dialogFormVisible = true"></el-button>
      </div>
    </div>
    <div class="type-table">
      <el-table
              :data="this.store.deviceTypeData"
              style="width: 100%">
        <el-table-column align="center" fixed label="id" min-width="100" prop="id"/>
        <el-table-column align="center" label="" min-width="100" prop="typeName"/>
        <!--              -->
        <el-table-column fixed="right" width="200" align="right">
          <template slot="header" slot-scope="scope">
            <el-input v-model="search" size="medium" clearable @clear='getDeviceTypeData' @input="getSearch" placeholder=""/>
          </template>
          <template v-slot="scope">
            <el-button size="medium" type="warning" @click="editDevTypeData(scope.row.id) "></el-button>
            <el-button size="medium" type="danger" @click="deleteDevTypeDate(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
              layout="total, sizes, prev, pager, next, jumper" style="text-align: center;"
              @current-change="currentChange" :total="totalCount">
      </el-pagination>
      <!-- -->
      <el-dialog title="" :visible.sync="dialogFormVisible" width="40%">
        <el-form ref="form" :model="form" label-width="120px">
          <el-form-item label="" prop="devNumber">
            <el-input v-model="form.id" style="width: 300px"/>
          </el-form-item>
          <el-form-item label="" prop="devName">
            <el-input v-model="form.typeName" style="width: 300px"/>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="addDevType()"></el-button>
            <el-button @click="dialogFormVisible = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
      <!--        -->
      <el-dialog title="" :visible.sync="dialogChangeDevFormVisible">
        <el-form ref="form" :model="devTypeForm" label-width="80px">
          <el-form-item label="id">
            <el-input v-model="devTypeForm.id" />
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="devTypeForm.typeName"/>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitDevTypeData()"></el-button>
            <el-button @click="dialogChangeVisible = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import {addDeviceTypeServeData, deleteDeviceTypeServeData, getCurrentDeviceTypeServeData, updateDeviceTypeServeData} from "@/network/device";
import {store} from "@/store/store";
import {getCurrentRegionServeData} from "@/network/region";

export default {
  name: "DeviceTypeMana",
  inject:['reload'],
  data () {
    return {
      store,
      devTypeForm: {}, //
      PageName:'',
      search: '',
      pageNum:'1',
      pageSize:'10',
      totalCount:0,
      dialogFormVisible: false, // 
      dialogChangeDevFormVisible: false,  // 
      form: {
        id:'',
        typeName: '',
      },
    }
  },

  created() {
    this.getDeviceTypeData()
  },

  methods: {
    /**
     * 
     */
    getDeviceTypeData () {
      getCurrentDeviceTypeServeData(this.pageNum,this.pageSize).then(res => {
        this.store.deviceTypeData = res.data.rows
        this.totalCount = res.data.totalCount
      }).catch(err => {
        console.log(err)
      })
    },

    /**
     *  
     */
    currentChange (val) {
      this.pageNum = val
      getCurrentDeviceTypeServeData(this.pageNum,this.pageSize).then(res => {
        this.store.deviceTypeData = res.data.rows
      }).catch(err => {
        console.log(err)
      })
    },

    /**
     * 
     */
    sizeChange (val) {
      this.pageSize = val
      getCurrentDeviceTypeServeData(this.pageNum,this.pageSize).then(res => {
        this.store.deviceTypeData = res.data.rows
      }).catch(err => {
        console.log(err)
      })
    },

    /**
     * 
     */
    addDevType () {
      addDeviceTypeServeData(this.form).then(res => {
        console.log(res)
        this.reload()
      }).catch(err => {
        console.log(err)
        alert(''+ err.code)
      })
      this.dialogFormVisible = false
    },

    /**
     * 
     * @param devNum 
     */
    editDevTypeData (devNum) {
      this.dialogChangeDevFormVisible = true
      this.store.deviceTypeData.map((item) => {
        if (devNum === item.id) {
          const c = JSON.parse(JSON.stringify(item))
          this.devTypeForm = c
        }
      })
    },

    /**
     * 
     */
    submitDevTypeData () {
      updateDeviceTypeServeData(this.devTypeForm).then(res => {
        console.log(res)
        this.reload()
      }).catch(err => {
        console.log(err)
      })
      this.dialogChangeDevFormVisible = false
    },

    /**
     * 
     * @param id id
     */
    deleteDevTypeDate (id) {
      deleteDeviceTypeServeData(id).then(res => {
        this.reload()
      }).catch(err => {
        console.log('err',err)
      })
    },

    /**
     * 
     */
    getSearch () {
      let keyWord = this.search.toLowerCase()
      let arr = []
      //
      if (keyWord === '') {
        this.getDeviceTypeData()
      }
      arr = this.store.deviceTypeData.filter(item => {
        if (item.typeName.toLowerCase().indexOf(keyWord) !== -1) {
          return item
        }
      })
      this.totalCount = arr.length
      this.store.deviceTypeData = arr
    },
  },

  }
</script>

<style scoped>
  .type-line {
    display: flex;
    margin-bottom: 10px;
    justify-content: space-between;
    width: 100%;
  }

  .type-line-path {
    height: 40px;
    font-size: 18px;
    font-weight: 400;
    line-height: 40px;
    color: rgba(0,0,0,.85);
  }

  .type-table {
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  }
</style>