import {request} from "@/network/request";

/**
 * 
 */

/**
 * 
 */
export function getDeviceServeData () {
  return request({
    url:'/device/selectAll',
    method:'get'
  })
}

/**
 * 
 * @param pageNum 
 * @param pageSize 
 */
export function getCurrentDeviceServeData (pageNum,pageSize) {
  return request({
    url:'/device/selectAll',
    method:'get',
    params:{
      currentPage:pageNum,
      pageSize:pageSize,
    }
  })
}

/**
 * 
 */
export function addDeviceServeData (obj) {
  return request({
    url:'/device/add',
    method:'post',
    params:obj
  })
}

/**
 * 
 */
export function deleteDeviceServeData (id) {
  return request({
    url:`/device/${id}`,
    method:'delete',
  })
}

/**
 * 
 */
export function updateDeviceServeData (obj) {
  return request({
    url:'/device/update',
    method:'put',
    params:obj,
  })
}

/**
 * 
 */
export function searchDeviceServeData (obj) {
  return request({
    url:'/device/selectCondition',
    method:'get',
    params:obj
  })
}

/**
 * 
 */
export function searchDeviceStateServeData (id) {
  return request({
    url:'/heartBeat/state',
    method:'post',
    params:{
      devId:id
    }
  })
}

